
'use server';

import {
  detectDrugInteractions,
  type DetectDrugInteractionsOutput,
} from '@/ai/flows/detect-drug-interactions';
import {
  parsePrescription,
  type ParsePrescriptionOutput,
} from '@/ai/flows/parse-prescription-flow';
import {
  medicationChat,
  type MedicationChatOutput,
} from '@/ai/flows/chatbot-flow';
import {
  explainInteractionRisks,
  type ExplainInteractionRisksOutput,
} from '@/ai/flows/explain-interaction-risks';
import {
  identifyPill,
  type IdentifyPillOutput,
} from '@/ai/flows/identify-pill-flow';
import {
    findGenericAlternatives,
    type FindGenericAlternativesOutput
} from '@/ai/flows/find-generic-alternatives';
import {
    transcribeMedicationAudio,
    type TranscribeMedicationAudioOutput
} from '@/ai/flows/voice-to-text';


export async function checkInteractions(
  drugList: string[]
): Promise<DetectDrugInteractionsOutput | { error: string }> {
  if (!drugList || drugList.length < 2) {
    return { error: 'Please provide at least two drugs to check for interactions.' };
  }

  try {
    const result = await detectDrugInteractions({ drugList });
    return result;
  } catch (error) {
    console.error('Error in detectDrugInteractions flow:', error);
    return { error: 'Failed to check interactions due to a server error. Please try again later.' };
  }
}

export async function getDrugsFromPrescription(
  photoDataUri: string
): Promise<ParsePrescriptionOutput | { error: string }> {
  if (!photoDataUri) {
    return { error: 'Please provide a prescription image.' };
  }

  try {
    const result = await parsePrescription({ photoDataUri });
    return result;
  } catch (error) {
    console.error('Error in parsePrescription flow:', error);
    return {
      error:
        'Failed to parse prescription due to a server error. Please try again later.',
    };
  }
}

export async function askChatbot(
  question: string,
  drugList?: string[],
  attachmentDataUri?: string
): Promise<MedicationChatOutput | { error: string }> {
  if (!question) {
    return { error: 'Please provide a question.' };
  }

  try {
    const result = await medicationChat({ question, drugList, attachmentDataUri });
    return result;
  } catch (error) {
    console.error('Error in medicationChat flow:', error);
    return { error: 'Failed to get a response due to a server error. Please try again later.' };
  }
}

export async function getRiskExplanation(
  drugA: string,
  drugB: string,
  interactionDetails: string
): Promise<ExplainInteractionRisksOutput | { error: string }> {
  if (!drugA || !drugB || !interactionDetails) {
    return { error: 'Please provide the necessary details for an explanation.' };
  }

  try {
    const result = await explainInteractionRisks({ drugA, drugB, interactionDetails });
    return result;
  } catch (error) {
    console.error('Error in explainInteractionRisks flow:', error);
    return { error: 'Failed to get an explanation due to a server error. Please try again later.' };
  }
}

export async function getPillIdentification(
  photoDataUri: string,
  imprint?: string,
  color?: string,
  shape?: string
): Promise<IdentifyPillOutput | { error: string }> {
  if (!photoDataUri) {
    return { error: 'Please provide a photo of the pill.' };
  }
  
  try {
    const result = await identifyPill({ photoDataUri, imprint, color, shape });
    return result;
  } catch (error) {
    console.error('Error in identifyPill flow:', error);
    return { error: 'Failed to identify pill due to a server error. Please try again later.' };
  }
}

export async function getGenericAlternatives(
    drugName: string
): Promise<FindGenericAlternativesOutput | { error: string }> {
    if (!drugName) {
        return { error: 'Please provide a drug name.' };
    }

    try {
        const result = await findGenericAlternatives({ drugName });
        return result;
    } catch (error) {
        console.error('Error in findGenericAlternatives flow:', error);
        return { error: 'Failed to find alternatives due to a server error. Please try again later.' };
    }
}

export async function getTranscription(
    audioDataUri: string
): Promise<TranscribeMedicationAudioOutput | { error: string }> {
    if (!audioDataUri) {
        return { error: 'Please provide an audio file.' };
    }

    try {
        const result = await transcribeMedicationAudio({ audioDataUri });
        return result;
    } catch (error) {
        console.error('Error in transcribeMedicationAudio flow:', error);
        return { error: 'Failed to transcribe audio due to a server error. Please try again later.' };
    }
}
