
'use client';

import { useState, useEffect, useMemo, useCallback } from 'react';
import { useForm, Controller } from 'react-hook-form';
import { zodResolver } from '@hookform/resolvers/zod';
import * as z from 'zod';
import { Button } from '@/components/ui/button';
import { Card, CardContent, CardDescription, CardHeader, CardTitle, CardFooter } from '@/components/ui/card';
import { Input } from '@/components/ui/input';
import { Label } from '@/components/ui/label';
import { useToast } from '@/hooks/use-toast';
import { AlertCircle, PlusCircle, User, Pill, Hash, Loader2, Trash2, Search, CheckCircle2, RefreshCw, BriefcaseMedical, Stethoscope, Bell, BadgeHelp } from 'lucide-react';
import { Accordion, AccordionContent, AccordionItem, AccordionTrigger } from '@/components/ui/accordion';
import { Badge } from '@/components/ui/badge';
import { checkInteractions } from '@/app/actions';
import { type DetectDrugInteractionsOutput } from '@/ai/flows/detect-drug-interactions';
import { PrescriptionUpload } from './prescription-upload';
import { Alert, AlertDescription, AlertTitle } from '@/components/ui/alert';
import {
  Dialog,
  DialogContent,
  DialogHeader,
  DialogTitle,
  DialogDescription,
  DialogFooter,
  DialogTrigger,
  DialogClose,
} from '@/components/ui/dialog';
import { Form, FormControl, FormField, FormItem, FormLabel, FormMessage } from '@/components/ui/form';


interface Medication {
  id: number;
  name: string;
  dosage: string;
  frequency: string;
  form: string;
  pills: number;
  refillsLeft: number;
  rxNumber: string;
  doctor: string;
}

const initialMedications: Medication[] = [
  { id: 1, name: 'Lisinopril', dosage: '10mg', frequency: 'Once daily', form: 'Tablet', pills: 30, refillsLeft: 2, rxNumber: 'RX789123', doctor: 'Dr. Smith' },
  { id: 2, name: 'Metformin', dosage: '500mg', frequency: 'Twice daily', form: 'Tablet', pills: 5, refillsLeft: 1, rxNumber: 'RX456789', doctor: 'Dr. Smith' },
  { id: 3, name: 'Simvastatin', dosage: '20mg', frequency: 'Once daily', form: 'Tablet', pills: 30, refillsLeft: 0, rxNumber: 'RX123456', doctor: 'Dr. Jones' },
];

const addMedicationSchema = z.object({
  name: z.string().min(1, 'Drug name is required.'),
  dosage: z.string().optional(),
  frequency: z.string().optional(),
  form: z.string().optional(),
  pills: z.coerce.number().int().min(0).optional(),
  refillsLeft: z.coerce.number().int().min(0).optional(),
  rxNumber: z.string().optional(),
  doctor: z.string().optional(),
});
type AddMedicationValues = z.infer<typeof addMedicationSchema>;

const quickCheckSchema = z.object({
    drugA: z.string().min(1, 'Drug A is required.'),
    drugB: z.string().min(1, 'Drug B is required.'),
});
type QuickCheckValues = z.infer<typeof quickCheckSchema>;

export function PatientDashboard() {
  const [patientName, setPatientName] = useState('Jane Doe');
  const [membershipNumber, setMembershipNumber] = useState('MEM123456789');
  const [medications, setMedications] = useState<Medication[]>(initialMedications);
  
  const [isChecking, setIsChecking] = useState(false);
  const [interactions, setInteractions] = useState<DetectDrugInteractionsOutput['interactions']>([]);
  const { toast } = useToast();

  const [isQuickChecking, setIsQuickChecking] = useState(false);
  const [quickCheckResult, setQuickCheckResult] = useState<DetectDrugInteractionsOutput['interactions'][0] | null>(null);
  const [noInteractionFound, setNoInteractionFound] = useState(false);

  const addMedicationForm = useForm<AddMedicationValues>({ resolver: zodResolver(addMedicationSchema) });
  const quickCheckForm = useForm<QuickCheckValues>({ resolver: zodResolver(quickCheckSchema) });

  const runInteractionCheck = useCallback(async (drugList: string[]) => {
    if (drugList.length < 2) {
      setInteractions([]);
      return;
    }
    setIsChecking(true);
    const response = await checkInteractions(drugList);
    if (response && 'error' in response) {
      toast({ variant: 'destructive', title: 'Error', description: response.error });
      setInteractions([]);
    } else {
      setInteractions(response.interactions || []);
      if (response.interactions && response.interactions.length > 0) {
        toast({ title: 'Interaction Alert', description: 'A potential drug interaction was detected. Please review your medications.' });
      }
    }
    setIsChecking(false);
  }, [toast]);

  useEffect(() => {
    // Save medications to local storage whenever they change so the chatbot can access them.
    localStorage.setItem('medications', JSON.stringify(medications));
    if (medications.length > 1) {
      runInteractionCheck(medications.map(m => m.name));
    } else {
        setInteractions([]);
    }
  }, [medications, runInteractionCheck]);

  const handleAddDrug = async (values: AddMedicationValues) => {
    const newMed: Medication = {
      id: Date.now(),
      name: values.name,
      dosage: values.dosage || 'N/A',
      frequency: values.frequency || 'N/A',
      form: values.form || 'Unknown',
      pills: values.pills || 0,
      refillsLeft: values.refillsLeft || 0,
      rxNumber: values.rxNumber || 'N/A (Self-Added)',
      doctor: values.doctor || 'Self-Added'
    };
    
    setMedications(prev => [...prev, newMed]);
    addMedicationForm.reset();
    toast({ title: 'Medication Added', description: `${newMed.name} has been added to your list.` });
    return true; // To close dialog
  };

  const removeDrug = (id: number) => {
    setMedications(prev => prev.filter(med => med.id !== id));
  };

  const handleDrugsParsed = (drugList: string[]) => {
    if (drugList.length === 0) {
      toast({ variant: 'destructive', title: 'No Drugs Found', description: "We couldn't find any medication names in the image." });
      return;
    }

    const newMeds: Medication[] = drugList.map(name => ({
      id: Date.now() + Math.random(),
      name,
      dosage: 'N/A',
      frequency: 'N/A',
      form: 'Unknown',
      pills: 0,
      refillsLeft: 0,
      rxNumber: 'N/A (From Upload)',
      doctor: 'From Prescription',
    }));

    setMedications(prev => [...prev, ...newMeds]);
    toast({ title: 'Medications Added', description: `${newMeds.length} medication(s) have been added to your list.` });
  };


  const getInteractionStatus = (drugName: string) => {
    return interactions.some(int => int.drugA.toLowerCase() === drugName.toLowerCase() || int.drugB.toLowerCase() === drugName.toLowerCase());
  };

  const handleQuickCheck = async (values: QuickCheckValues) => {
    setIsQuickChecking(true);
    setQuickCheckResult(null);
    setNoInteractionFound(false);

    const response = await checkInteractions([values.drugA, values.drugB]);

    if (response && 'error' in response) {
      toast({ variant: 'destructive', title: 'Error', description: response.error });
    } else if (response.interactions && response.interactions.length > 0) {
      setQuickCheckResult(response.interactions[0]);
    } else {
      setNoInteractionFound(true);
    }
    setIsQuickChecking(false);
    quickCheckForm.reset();
  };

  const lowStockMeds = useMemo(() => {
    return medications.filter(med => med.pills > 0 && med.pills < 7);
  }, [medications]);

  const noRefillsMeds = useMemo(() => {
    return medications.filter(med => med.refillsLeft === 0);
  }, [medications]);

  return (
    <div className="space-y-6">
      <Card>
          <CardHeader>
            <CardTitle className="flex items-center gap-2"><User /> Patient Information</CardTitle>
          </CardHeader>
          <CardContent>
            <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
              <div className="space-y-2">
                  <Label htmlFor="patientName">Patient Name</Label>
                  <Input 
                      id="patientName" 
                      value={patientName} 
                      onChange={(e) => setPatientName(e.target.value)}
                      placeholder="Enter patient name"
                  />
              </div>
              <div className="space-y-2">
                  <Label htmlFor="membershipNumber">Membership No.</Label>
                  <Input 
                      id="membershipNumber" 
                      value={membershipNumber}
                      onChange={(e) => setMembershipNumber(e.target.value)}
                      placeholder="Enter membership number"
                  />
              </div>
            </div>
          </CardContent>
        </Card>

        {(lowStockMeds.length > 0 || noRefillsMeds.length > 0) && (
            <Card>
                <CardHeader>
                    <CardTitle className="flex items-center gap-2 text-orange-600"><Bell /> Refill Reminders</CardTitle>
                </CardHeader>
                <CardContent className="space-y-4">
                    {lowStockMeds.length > 0 && (
                         <Alert variant="destructive">
                            <AlertCircle className="h-4 w-4" />
                            <AlertTitle>Low Stock Alert</AlertTitle>
                            <AlertDescription>
                                The following medications are running low: {lowStockMeds.map(m => `${m.name} (${m.pills} pills left)`).join(', ')}. Please request a refill soon.
                            </AlertDescription>
                        </Alert>
                    )}
                     {noRefillsMeds.length > 0 && (
                         <Alert>
                            <AlertCircle className="h-4 w-4" />
                            <AlertTitle>No Refills Left</AlertTitle>
                            <AlertDescription>
                                You are out of refills for: {noRefillsMeds.map(m => m.name).join(', ')}. Please contact your doctor.
                            </AlertDescription>
                        </Alert>
                    )}
                </CardContent>
            </Card>
        )}

      <div className="grid grid-cols-1 lg:grid-cols-3 gap-6">
          <div className="lg:col-span-3 space-y-6">
             <Card>
              <CardHeader>
                <CardTitle className="flex items-center justify-between">
                    My Medications
                    {isChecking && <Loader2 className="animate-spin text-muted-foreground"/>}
                </CardTitle>
                <CardDescription>A list of your current medications. Interaction alerts will appear here.</CardDescription>
                 <div className="flex gap-2 pt-4">
                    <Dialog>
                        <DialogTrigger asChild>
                            <Button><PlusCircle className="mr-2"/> Add Medication</Button>
                        </DialogTrigger>
                        <DialogContent>
                            <DialogHeader>
                                <DialogTitle>Add a New Medication</DialogTitle>
                                <DialogDescription>Enter the details of your new medication below.</DialogDescription>
                            </DialogHeader>
                            <Form {...addMedicationForm}>
                                <form onSubmit={addMedicationForm.handleSubmit(async (data) => {
                                    const success = await handleAddDrug(data);
                                    if (success) {
                                       const closeButton = document.getElementById('close-add-med-dialog');
                                       if(closeButton) closeButton.click();
                                    }
                                })}>
                                    <div className="grid grid-cols-2 gap-4 py-4">
                                        <FormField control={addMedicationForm.control} name="name" render={({ field }) => (<FormItem className="col-span-2"><FormLabel>Drug Name*</FormLabel><FormControl><Input placeholder="e.g., Lisinopril" {...field} /></FormControl><FormMessage /></FormItem>)} />
                                        <FormField control={addMedicationForm.control} name="dosage" render={({ field }) => (<FormItem><FormLabel>Dosage</FormLabel><FormControl><Input placeholder="e.g., 10mg" {...field} /></FormControl><FormMessage /></FormItem>)} />
                                        <FormField control={addMedicationForm.control} name="frequency" render={({ field }) => (<FormItem><FormLabel>Frequency</FormLabel><FormControl><Input placeholder="e.g., Once daily" {...field} /></FormControl><FormMessage /></FormItem>)} />
                                        <FormField control={addMedicationForm.control} name="form" render={({ field }) => (<FormItem><FormLabel>Form</FormLabel><FormControl><Input placeholder="e.g., Tablet" {...field} /></FormControl><FormMessage /></FormItem>)} />
                                        <FormField control={addMedicationForm.control} name="pills" render={({ field }) => (<FormItem><FormLabel>Pill Count</FormLabel><FormControl><Input type="number" {...field} /></FormControl><FormMessage /></FormItem>)} />
                                        <FormField control={addMedicationForm.control} name="refillsLeft" render={({ field }) => (<FormItem><FormLabel>Refills Left</FormLabel><FormControl><Input type="number" {...field} /></FormControl><FormMessage /></FormItem>)} />
                                        <FormField control={addMedicationForm.control} name="rxNumber" render={({ field }) => (<FormItem><FormLabel>Rx Number</FormLabel><FormControl><Input placeholder="e.g., RX123456" {...field} /></FormControl><FormMessage /></FormItem>)} />
                                        <FormField control={addMedicationForm.control} name="doctor" render={({ field }) => (<FormItem className="col-span-2"><FormLabel>Prescribing Doctor</FormLabel><FormControl><Input placeholder="e.g., Dr. Smith" {...field} /></FormControl><FormMessage /></FormItem>)} />
                                    </div>
                                    <DialogFooter>
                                        <DialogClose asChild><Button type="button" variant="secondary" id="close-add-med-dialog">Cancel</Button></DialogClose>
                                        <Button type="submit" disabled={addMedicationForm.formState.isSubmitting}>
                                            {addMedicationForm.formState.isSubmitting && <Loader2 className="animate-spin mr-2"/>}
                                            Save Medication
                                        </Button>
                                    </DialogFooter>
                                </form>
                            </Form>
                        </DialogContent>
                    </Dialog>
                    <Dialog>
                        <DialogTrigger asChild>
                            <Button variant="outline">Upload Prescription</Button>
                        </DialogTrigger>
                        <DialogContent>
                            <DialogHeader>
                                <DialogTitle>Upload Prescription</DialogTitle>
                                <DialogDescription>Upload an image of your prescription to automatically add medications.</DialogDescription>
                            </DialogHeader>
                            <PrescriptionUpload onDrugsParsed={handleDrugsParsed} />
                        </DialogContent>
                    </Dialog>
                    <Dialog>
                        <DialogTrigger asChild>
                            <Button variant="outline">Quick Interaction Check</Button>
                        </DialogTrigger>
                        <DialogContent>
                            <DialogHeader>
                                <DialogTitle>Quick Interaction Check</DialogTitle>
                                <DialogDescription>Check for interactions between two drugs without adding them to your list.</DialogDescription>
                            </DialogHeader>
                            <Form {...quickCheckForm}>
                                <form onSubmit={quickCheckForm.handleSubmit(handleQuickCheck)} className="space-y-4 pt-4">
                                     <div className="grid grid-cols-1 sm:grid-cols-2 gap-4">
                                        <FormField control={quickCheckForm.control} name="drugA" render={({ field }) => (<FormItem><FormLabel>Drug A</FormLabel><FormControl><Input placeholder="e.g., Lisinopril" {...field} /></FormControl><FormMessage /></FormItem>)} />
                                        <FormField control={quickCheckForm.control} name="drugB" render={({ field }) => (<FormItem><FormLabel>Drug B</FormLabel><FormControl><Input placeholder="e.g., Ibuprofen" {...field} /></FormControl><FormMessage /></FormItem>)} />
                                    </div>
                                    <Button type="submit" disabled={isQuickChecking} className="w-full">
                                        {isQuickChecking ? <Loader2 className="animate-spin mr-2" /> : <Search className="mr-2" />}
                                        Check for Interaction
                                    </Button>
                                </form>
                            </Form>
                            {isQuickChecking && <div className="flex justify-center items-center pt-4"><Loader2 className="mr-2 h-5 w-5 animate-spin" /><span>Checking...</span></div>}
                            {quickCheckResult && (
                                <Card className="mt-4 bg-muted/50">
                                    <CardHeader><CardTitle className="flex items-center justify-between text-lg">Interaction Result<Badge variant={quickCheckResult.riskLevel === 'High' ? 'destructive' : quickCheckResult.riskLevel === 'Moderate' ? 'warning' : 'secondary'}>{quickCheckResult.riskLevel} Risk</Badge></CardTitle></CardHeader>
                                    <CardContent className="space-y-2">
                                        <p>An interaction was found between <strong>{quickCheckResult.drugA}</strong> and <strong>{quickCheckResult.drugB}</strong>.</p>
                                        <p className="text-muted-foreground text-sm">{quickCheckResult.description}</p>
                                        {quickCheckResult.alternatives && quickCheckResult.alternatives.length > 0 && (
                                            <div className="pt-2"><h4 className="font-semibold">Suggested Alternatives:</h4><p className="text-muted-foreground text-sm">{quickCheckResult.alternatives.join(', ')}</p></div>
                                        )}
                                    </CardContent>
                                </Card>
                            )}
                            {noInteractionFound && (
                                <Alert className="mt-4"><CheckCircle2 className="h-4 w-4" /><AlertTitle>No Significant Interactions Found</AlertTitle><AlertDescription>No significant interactions were found between the two drugs entered. Always consult your healthcare provider.</AlertDescription></Alert>
                            )}
                        </DialogContent>
                    </Dialog>
                </div>
              </CardHeader>
              <CardContent className="space-y-4">
                {medications.length === 0 ? (
                    <div className="text-center py-8 text-muted-foreground border-2 border-dashed rounded-lg">You have no medications saved.</div>
                ) : (
                    <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                        {medications.map((med) => (
                        <Card key={med.id} className={getInteractionStatus(med.name) ? "border-destructive" : ""}>
                            <CardContent className="pt-6">
                            <div className="flex justify-between items-start">
                                <div>
                                <h3 className="font-bold text-lg flex items-center gap-2">
                                    <Pill size={20} /> {med.name}
                                    {getInteractionStatus(med.name) && (
                                        <Badge variant="destructive" className="flex items-center gap-1"><AlertCircle size={14} /> Interaction</Badge>
                                    )}
                                </h3>
                                <p className="text-sm text-muted-foreground">{med.dosage} - {med.frequency} - {med.form}</p>
                                </div>
                                <Button variant="ghost" size="icon" onClick={() => removeDrug(med.id)} className="text-muted-foreground hover:text-destructive -mt-2 -mr-2"><Trash2 size={16} /></Button>
                            </div>
                            <Accordion type="single" collapsible className="w-full">
                                <AccordionItem value="item-1" className="border-b-0">
                                    <AccordionTrigger className="text-sm pt-2 pb-0">View Details</AccordionTrigger>
                                    <AccordionContent className="text-muted-foreground space-y-1 pt-2 text-sm">
                                    <p className="flex items-center gap-2"><Hash size={14}/> <strong>RX Number:</strong> {med.rxNumber}</p>
                                    <p className="flex items-center gap-2"><Stethoscope size={14} /> <strong>Prescribed by:</strong> {med.doctor}</p>
                                    <p className="flex items-center gap-2"><BriefcaseMedical size={14} /> <strong>Pill Count:</strong> {med.pills}</p>
                                    <p className="flex items-center gap-2"><RefreshCw size={14} /> <strong>Refills Left:</strong> {med.refillsLeft}</p>
                                    <div className="pt-2">
                                        <Button size="sm" variant="outline" className="h-8"><BadgeHelp className="mr-2 h-4 w-4"/>Find Generic Alternatives</Button>
                                    </div>
                                    </AccordionContent>
                                </AccordionItem>
                            </Accordion>
                            </CardContent>
                        </Card>
                        ))}
                    </div>
                )}
              </CardContent>
            </Card>
          </div>
      </div>
    </div>
  );
}
